import styled, { css } from "styled-components";

export const disableStyle = css`
  opacity: 0.4;
  cursor: not-allowed;
`;

export const Button = styled.button`
  width: 100%;
  height: 100%;
  letter-spacing: 0.02857em;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 14px;
  background-color: var(--buttonColor);
  color: var(--text_color);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border: none;
  outline: none;

  :hover {
    background-color: var(--bg_hover_color);
  }

  ${({ extraStyling }) => extraStyling && extraStyling}
  ${({ disabled }) => disabled && disableStyle}
`;


export const LoaderWrapper = styled.div`
    height: 150px;
    width:150px ;
`;