import styled from "styled-components";
export const Container = styled.div`
  min-height: 704px;
  .product {
    text-decoration: none;
  }
  width: 100%;
  border: solid var(--borderColor) 1px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 980px;
`;
export const ProductBlock = styled.div`
  margin: .5rem;
  padding: 1.5rem;
  text-align: left;
  color: var(--text_color);
  text-decoration: none;
  border: 1px solid var(--borderColor);
  border-radius: 10px;
  transition: color 0.15s ease, border-color 0.15s ease;
  width: 300px;
  height:400px ;
  display: flex;
  flex-direction: column;
  justify-content:space-between ;
  cursor: pointer;
  :hover,
  :focus,
  :active {
    border-color: var(--linkColor);
    background: none;
  }
  h2 {
    margin: 0 0 1rem 0;
    font-size: 1.5rem;
    text-align: center;
  }
  p {
    margin: 0;
    font-size: 1.25rem;
    line-height: 1.5;
  }
  img {
    width: 150px;
    height: auto;
    align-self: center;
  }
`;
