import React, { useEffect, useRef, useState } from "react";
import { Container, Loader } from "./CircleLoader.style";

const CircleLoader = ({ loaderSize = 2, color = "#848484", className }) => {
  const ContainerRef = useRef(null);
  const [wrappingSquareSize, setWrappingSquareSize] = useState("");

  useEffect(() => {
    const y = ContainerRef?.current?.clientHeight;
    const x = ContainerRef?.current?.clientWidth;
    const size = x >= y ? y : x;
    size && setWrappingSquareSize(`${size}px`);
  }, [ContainerRef]);
  return (
    <Container className={className} ref={ContainerRef}>
        <Loader
          className='Loader'
          loaderSize={loaderSize}
          color={color}
          size={wrappingSquareSize}
        />
    </Container>
  );
};

export default CircleLoader;
