import styled, { css, keyframes } from "styled-components";
const PageLoader = keyframes`
from {
         transform: rotate(0deg);
     }
     to {
         transform: rotate(360deg);
     }
`;

export const Container = styled.div`
         width: 100%;
         height: 100%;
         display: flex;
         justify-content: center;
         align-items: center;
       `;

export const Loader = styled.div`

  box-sizing: border-box;
  width: ${(props) => props.size && props.size};
  height: ${(props) => props.size && props.size};
  &.isIE11 {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  content: "";
  border-radius: 50%;
  ${({ loaderSize }) =>
    loaderSize &&
    css`
      border: ${loaderSize}px solid transparent;
    `}
  border-top-color:${(props) => (props.color ? props.color : "#848484")};
  animation: ${PageLoader} 0.6s linear infinite;
  -webkit-animation: ${PageLoader} 0.6s linear infinite;

${({ extraStyling }) => extraStyling && extraStyling}`;
